.actionButton {
    @apply font-acumin-pro rounded-3xl px-6 pt-2 pb-1.5 text-sm text-eco-dark flex flex-row justify-between items-center w-fit my-2 md:hover:animate-pulse duration-300 ease-in-out;
}

.submitButton {
    @apply font-acumin-pro bg-eco-dark px-6 py-1 text-sm rounded-3xl border-2 flex flex-row items-center justify-between w-fit my-2 md:hover:animate-pulse duration-300 ease-in-out;
}

.detailsButton {
    @apply text-white text-xs px-2 py-1 font-acumin-pro rounded-3xl  md:hover:animate-pulse duration-300 ease-in-out;
}

.statsDate {
    @apply font-acumin-pro text-sm;
}