.titleWrapper {
    @apply flex flex-row justify-start items-center mb-5;
}

.sectionTitle {
    @apply text-6xl font-acumin-pro font-bold mb-5;
}

.sectionSubTitle {
    @apply text-3xl font-acumin-pro font-bold mt-5;
}

.orgWrapper {
    @apply grid gap-7 mt-8;
    @apply sm:grid-cols-2 sm:gap-7;
    @apply md:grid-cols-3 md:gap-6;
    @apply lg:grid-cols-4 lg:gap-4;
}

.orgCard {
    @apply cursor-pointer py-3 border-t-2 my-4 border-t-eco-grey;
}

.orgCardTitle {
    @apply font-bold cursor-pointer tracking-wide font-acumin-pro text-eco-white text-lg hover:underline;
}

.pageHead {
    @apply text-4xl tracking-wide tracking-wide font-acumin-pro font-bold pb-12;
}


.projectWrapper {
    @apply grid gap-7 mt-8;
    @apply sm:grid-cols-2 sm:gap-7;
    @apply md:grid-cols-3 md:gap-6;
    @apply lg:grid-cols-4 lg:gap-4;
}

.projectCard {
    @apply cursor-pointer py-3 border-t my-4 border-t-eco-grey;
}

.projectDirectory {
    @apply text-eco-grey uppercase font-medium tracking-wide text-[11px] mb-2;
}

.projectCardTitle {
    @apply tracking-wide capitalize font-acumin-pro text-eco-white text-2xl;
}

.projectCard:hover .projectCardTitle {
    @apply underline; 
}

.statsEditButton {
    @apply ml-2.5 text-xs text-eco-white px-3 pt-1 bg-eco-purple rounded-3xl mb-2;
}

