.inputWrapper {
    @apply flex flex-col mb-4;
}

.inputLabel {
    @apply text-eco-white text-sm mb-1 font-acumin-pro;
}

.inputField {
    @apply outline-none text-sm text-white px-2 pt-2 pb-1.5 rounded bg-transparent w-full  font-acumin-pro border-2;
}

.inputFieldWrapper {
    @apply flex flex-row w-full justify-between items-center rounded bg-eco-white bg-opacity-20 pr-2;
}

.inputIcon {
    @apply h-6 w-6 text-eco-white invert;
}

.errorMessage {
    @apply text-eco-error font-acumin-pro text-sm mt-2 font-bold;
}